/**
 * #.# Editor Styles
 *
 * CSS for just Backend enqueued after style.scss
 * which makes it higher in priority.
 */
.kadence-toolbar-design-library {
  margin-left: 5px;
  margin-right: 5px;
  align-items: center;
}
.kadence-toolbar-design-library .kb-toolbar-prebuilt-button {
  padding-left: 12px;
  padding-right: 12px;
}

@media (max-width: 600px) {
  .kadence-toolbar-design-library {
    display: none;
  }
}
.kt-block-default-palette .components-color-palette__item {
  display: flex;
  align-items: center;
  justify-content: center;
  vertical-align: top;
  height: 100%;
  width: 100%;
  border: none;
  border-radius: 50%;
  background: transparent;
  box-shadow: inset 0 0 0 14px;
  transition: box-shadow 0.1s ease;
  cursor: pointer;
}

.kt-colors-add-new {
  margin-bottom: 16px;
}

.kb-colors-show-notice {
  background: #fef8ee;
  padding: 10px;
  color: #222;
}

.components-popover.kt-popover-color > .components-popover__content > .components-base-control {
  padding: 0 10px;
}

.kt-block-default-palette .components-color-palette, .components-popover.kt-popover-color .components-color-palette {
  margin-right: 0;
  width: auto;
}

.kt-block-defaults-modal .components-panel__body .components-base-control.components-color-picker__inputs-field {
  margin-bottom: 0;
}

.components-popover.kt-popover-color .components-color-palette__item-wrapper, .kt-color-popover-container .components-color-palette__item-wrapper {
  display: inline-block;
  height: 28px;
  width: 28px;
  margin-right: 7px;
  margin-left: 7px;
  margin-bottom: 14px;
  vertical-align: top;
  transform: scale(1);
  transition: transform 0.1s ease;
}
.components-popover.kt-popover-color .components-color-palette__item-wrapper .components-color-palette__item, .kt-color-popover-container .components-color-palette__item-wrapper .components-color-palette__item {
  display: inline-block;
  vertical-align: top;
  height: 100%;
  width: 100%;
  border: none;
  border-radius: 50%;
  background: transparent;
  box-shadow: inset 0 0 0 14px;
  transition: box-shadow 0.1s ease;
  cursor: pointer;
}
.components-popover.kt-popover-color .components-color-palette__item-wrapper .components-color-palette__item:after, .kt-color-popover-container .components-color-palette__item-wrapper .components-color-palette__item:after {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  bottom: -1px;
  right: -1px;
  border-radius: 50%;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2);
  border: 1px solid transparent;
}
.components-popover.kt-popover-color .components-color-palette__item-wrapper .components-color-palette__item.is-active, .kt-color-popover-container .components-color-palette__item-wrapper .components-color-palette__item.is-active {
  box-shadow: inset 0 0 0 4px;
  position: relative;
  z-index: 1;
}
.components-popover.kt-popover-color .components-color-palette__item-wrapper .components-color-palette__item.is-active + .dashicons-saved, .kt-color-popover-container .components-color-palette__item-wrapper .components-color-palette__item.is-active + .dashicons-saved {
  position: absolute;
  left: 4px;
  top: 4px;
}
.components-popover.kt-popover-color .components-color-palette__item-wrapper .components-color-palette__item:focus, .kt-color-popover-container .components-color-palette__item-wrapper .components-color-palette__item:focus {
  outline: none;
}
.components-popover.kt-popover-color .components-color-palette__item-wrapper .components-color-palette__item:focus:after, .kt-color-popover-container .components-color-palette__item-wrapper .components-color-palette__item:focus:after {
  content: "";
  border: 2px solid #606a73;
  width: 32px;
  height: 32px;
  position: absolute;
  top: -2px;
  left: -2px;
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px #fff;
}

.kt-block-default-palette .components-color-palette__item-wrapper, .kt-color-popover-container .components-color-palette__item-wrapper {
  display: inline-block;
  height: 28px;
  width: 28px;
  margin-right: 14px;
  margin-bottom: 14px;
  vertical-align: top;
  transform: scale(1.1);
  transition: transform 0.1s ease;
}
.kt-block-default-palette .components-color-palette__item-wrapper .components-color-palette__item:after, .kt-color-popover-container .components-color-palette__item-wrapper .components-color-palette__item:after {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  bottom: -1px;
  right: -1px;
  border-radius: 50%;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2);
  border: 1px solid transparent;
}

.kt-block-default-palette .components-color-palette__item-wrapper:hover, .kt-color-popover-container .components-color-palette__item-wrapper:hover {
  transform: scale(1.1);
}

.kt-block-default-palette .components-color-palette__item svg {
  color: #646464;
}

.kt-colors-remove-last {
  display: inline-block;
  height: 28px;
  width: 28px;
  margin-right: 14px;
  margin-bottom: 14px;
}

.kt-colors-remove-last .components-button {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.kt-blocks-width-control, .kt-blocks-control-wrap {
  padding: 10px;
}

.components-button-group .kt-editor-width-btn {
  background: transparent;
  box-shadow: none;
  border: 1px solid #eee;
  padding: 8px;
  margin-right: 0px;
  height: auto;
  width: 100%;
  font-size: 13px;
  margin-bottom: 5px;
  text-shadow: none;
}

.components-button-group .kt-editor-width-btn:hover, .components-button-group .kt-editor-width-btn.is-primary, .components-button-group .kt-editor-width-btn:focus:not(:disabled):not([aria-disabled=true]) {
  border-color: #444;
  box-shadow: none;
  background: #777;
  color: white;
}

.components-button-group .kt-editor-width-btn.is-primary, .components-button-group .kt-editor-width-btn:focus:not(:disabled):not([aria-disabled=true]) {
  background: #444;
}

.kt-blocks-control-wrap .kt-block-defaults {
  display: inline-flex;
  padding: 4px 10px;
  font-size: 12px;
  font-weight: 600;
  align-items: center;
  line-height: 24px;
  background: #fff;
  border: 1px solid #ddd;
  text-align: left;
  color: #444;
  width: 80%;
  box-shadow: none;
  margin-bottom: 10px;
}
.kt-blocks-control-wrap .kt-block-defaults span.kt-block-icon {
  display: block;
  height: 20px;
  margin-right: 10px;
}

.kt-blocks-control-wrap .kt-block-defaults:hover {
  background: #f9f9f9;
}

.kt-blocks-control-wrap .kt-block-defaults:focus {
  box-shadow: none;
}

.kt-block-defaults-modal .components-panel__body .kadence-controls-content .components-base-control {
  margin-bottom: 0;
}

.kt-blocks-control-row {
  display: flex;
}

.kt-blocks-control-wrap .kt-block-settings {
  width: 16%;
  display: inline-flex;
  padding: 6px 10px;
  font-weight: 600;
  -ms-flex-align: center;
  align-items: center;
  background: #fff;
  border: 1px solid #ddd;
  text-align: left;
  color: #555;
  box-shadow: none;
  margin-left: 2%;
  margin-bottom: 10px;
}

.kt-blocks-control-wrap .kt-block-settings span.dashicon {
  width: auto;
  height: auto;
  font-size: 12px;
}

.kb-modal-footer {
  display: flex;
  justify-content: space-between;
}

.kt-blocks-control-wrap .kt-block-settings:hover {
  background: #f9f9f9;
}

.kt-blocks-control-wrap .kt-block-settings:focus {
  box-shadow: none;
}

.kt-block-settings-modal .components-modal__header h1, .kt-block-defaults-modal .components-modal__header h1 {
  font-size: 14px;
  font-weight: bold;
}

.kt-block-defaults-modal button.components-button.kt-defaults-save, .kt-block-settings-modal button.components-button.kt-settings-save {
  margin-top: 10px;
}

@media (min-width: 600px) {
  .kt-block-defaults-modal {
    width: 400px;
  }
}
.kt-block-defaults-modal p {
  margin-top: 0;
}

.kt-block-defaults-modal .components-color-palette {
  padding-bottom: 5px;
}
.kt-block-defaults-modal .components-color-palette:after {
  clear: both;
  display: table;
  content: "";
}

.kt-block-defaults-modal .components-panel__body .components-base-control {
  margin-bottom: 24px;
}

.kt-block-defaults-modal .components-panel__body:last-child {
  margin-bottom: -16px;
}

.kt-block-defaults-modal .components-panel__body:first-child {
  margin-top: 16px;
}

.kt-block-defaults-modal .components-panel__body {
  border: none;
  border-top: 1px solid #e2e4e7;
  margin: 0 -16px;
}

.kt-font-family-modal {
  min-height: 480px;
}

.kt-blocks-width-control .components-button-group {
  border: 0;
}